<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header" style="height: 100px">
        <!-- 搜索表单 start -->
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <!-- 请选择日期统计方式 start -->
          <el-form-item label="" prop="selectDateState">
            <el-select v-model="searchValidateForm.selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="startEndDate">
            <el-date-picker v-model="searchValidateForm.startEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 操作按钮 start -->
        <div>
          <!-- 重置 start -->
          <el-button type="primary" icon="el-icon-refresh-right" @click="onReset">重置</el-button>
          <!-- 重置 end -->

          <!-- 景区统计设置 start -->
          <el-button type="primary" icon="el-icon-s-tools" @click="onSetting('searchValidateForm')">景区统计设置</el-button>
          <!-- 景区统计设置 end -->

          <!-- 统计分析 start -->
          <el-button type="primary" icon="el-icon-data-line" @click="onStatGrossProfit('searchValidateForm')">统计分析</el-button>
          <!-- 统计分析end -->

          <!-- 导出报表 start -->
          <a href="" id="exportReport" download="运营时长分析">
            <el-button type="primary" icon="el-icon-s-data" @click="onExportReport">导出报表</el-button>
          </a>
          <!-- 导出报表 end -->

          <!-- 导出 Excel end -->

          <!-- 新增 start -->
          <!-- <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button> -->
          <!-- 新增 end -->
        </div>
        <!-- 操作按钮 end -->
      </el-header>
      <!-- 显示统计设置 start -->
      <div v-show="showTable">
        <el-main>
          <!-- 表格 start -->
          <el-table class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading" :default-sort="{prop: 'totalTime', order: 'descending'}" @sort-change="onSortChange" :header-cell-style="{ background: '#f2f2f2',color: '#606266', height: '38px', padding: '0px' }" :cell-style="{height: '38px',padding: '0'}">
            <!-- 序号 start -->
            <el-table-column type="index" label="序号" width="50">
            </el-table-column>
            <!-- 序号 end -->
            <template v-for="(value, key) in tableFields">
              <el-table-column :key="key" :prop="value" :label="key" min-width="130" :fixed="activeFixed(value)">
                <template slot-scope="scope">
                  <div>
                    {{ tableContentValue(scope.row, value) }}
                  </div>
                </template>
              </el-table-column>
            </template>
            <el-table-column label="操作" min-width="130" fixed="right">
              <template slot-scope="scope">
                <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                  编辑
                </el-button>
                <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text" size="small">
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <!-- 表格 end -->

          <!-- 新增统计设置 start -->
          <el-button type="primary" class="add-btn" icon="el-icon-circle-plus-outline" @click="onAdd">新增统计设置</el-button>
          <!-- 新增统计设置 start -->
        </el-main>
        <el-footer>
          <!-- 分页 start -->
          <!-- <div class="block">
            <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
            </el-pagination>
          </div> -->
          <!-- 分页 end -->
        </el-footer>
      </div>
      <!-- 显示统计设置 end -->

      <!-- 显示柱状图 start -->
      <div v-show="!showTable" class="my-echarts">
        <div id="myChart"></div>
        <div>
          {{ sectionMessage }}
        </div>
      </div>
      <!-- 显示柱状图 end -->
      <div>
        <el-dialog class="dialog" title="新增统计设置" :visible.sync="dialogVisible" :before-close="handleClose" :close-on-click-modal="false">
          <!-- form表单 start -->
          <el-form :model="validateForm" ref="validateForm" label-width="170px" class="demo-ruleForm" :rules="validateFormRules">
            <el-form-item label="景区排序分段统计时长" prop="begin">
              <el-input type="number" style="width: 100px" v-model="validateForm.begin" autocomplete="off" placeholder="请输入起始分段" :disabled="disabledInput"></el-input>
            </el-form-item>
            <el-form-item class="section-end">
              <span>——</span>
            </el-form-item>
            <el-form-item label="" prop="end" style="width: 100px" class="section-end">
              <el-input type="number" style="width: 100px" v-model="validateForm.end" autocomplete="off" placeholder="请输入结束分段" :disabled="disabledInput"></el-input>
            </el-form-item>
          </el-form>
          <!-- 提交 start -->
          <div class="submit">
            <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
            <el-button @click="dialogClose">取消</el-button>
          </div>
          <!-- 提交 end -->
        </el-dialog>
      </div>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime } from "../utils/utils.js"
export default {
  name: 'ScenicProfitStatistic',
  data() {
    // /**
    // * 公共验证函数(验证数字值)
    // * @params message - 提示信息
    // */
    // function checkNumberMethod(name = "") {
    //   // 返回验证函数
    //   return (rule, value, callback) => {
    //     if (!value) {
    //       // 如果值为空，可以回调
    //       callback();
    //     }

    //     if (!Number(value)) {
    //       callback(new Error(name + '必须为数字值'));
    //     } else {
    //       callback();
    //     }
    //   }
    // }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
      showTable: false, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: "", // 搜索的手续费
      searchTaxInput: "", // 搜索的税点
      tableFields: { //导出Excel表格的表头设置
        "景区排序分段统计时长": "SECTION",
      },
      tableData: [], // 表格数据

      tableLoading: false,

      searchCompanyOptions: [], // 公司选项
      searchScenicOptions: [], // 景区选项
      dateStateOptions: [{
        value: '1',
        label: '按月查询'
      }, {
        value: '2',
        label: '按年查询'
      }], // 日期统计方式选项

      downloadLoading: false, // 导出excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      dateType: "monthrange", // 日期类型（月，日，年）
      valueFormat: "yyyy-MM", // 日期格式

      searchValidateForm: {
        selectDateState: "1", // 日期统计方式（日，月，年）
        startEndDate: [
          new Date(),
          new Date()
        ], // 起止日期
      }, // 搜索表单验证对象
      searchVF: {
        selectDateState: "1", // 日期统计方式（日，月，年）
        startEndDate: [
          new Date(),
          new Date()
        ], // 起止日期
      }, // 提交表单验证对象
      searchVFEcharts: {
        selectDateState: "1", // 日期统计方式（日，月，年）
        startEndDate: [
          new Date(),
          new Date()
        ], // 起止日期
      }, // 统计报表表单验证对象
      validateForm: {}, // 表单验证对象

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      total: 0,

      order: "totalTime desc", // 表格排序
      echartsOrder: null, // echarts图表排序

      myChart: null, // 柱状图echarts实例
      option: {}, // 柱状图optino

      spotNameListLen: 0, // 景区列表长度（限制新增分段的长度）
      sectionMessage: "",
      disabledInput: true, // 禁用输入表单内容

      // 搜索表单规则
      searchValidateFormRules: {

      },

      // 提交表单验证
      validateFormRules: {
        // 景区排序分段统计时长(起始分段)
        begin: [
          { validator: this.checkNumberMethod('起始分段统计时长'), required: true, trigger: ['blur', 'change'] }
        ],

        // 景区排序分段统计时长(结束分段)
        end: [
          { validator: this.checkNumberMethod('结束分段统计时长'), required: true, trigger: ['blur', 'change'] }
        ],
      }
    }
  },
  created() {
    this.getValidateForm()
    // this.login()

  },
  mounted() {
    this.drawEcharts()
    this.initTableData()
    this.initEchartsData()
  },
  methods: {
    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback();
        }

        console.log(value);
        if (!Number(value)) {
          callback(new Error(name + '必须为正整数'));
        } else {
          if (Number(value) > this.spotNameListLen) {
            callback(new Error(name + "的长度不能大于" + this.spotNameListLen))
          } else {
            callback();
          }
        }
      }
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ""
      }
      this.validateForm = validateForm
      console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post("/loginSystem?username=lijiazhao&password=lijiazhao")
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      const { data: res } = await this.$http.get("/system/operational_analysis/fitList")
      console.log(res);
      this.tableData = res.data
      return res
    },

    /**
     * 初始化Echarts数据
     */
    async initEchartsData() {
      var selectDateState = this.searchVFEcharts.selectDateState
      if (selectDateState == "" || !selectDateState) {
        selectDateState = null
      }

      var startEndDate = this.searchVFEcharts.startEndDate
      var startDate, endDate
      if (startEndDate == "" || !startEndDate) {
        // 因为数据量太大，查的太慢，所以默认传入当前月
        // startDate = formatMonth()
        // endDate = formatMonth()
        startDate = null
        endDate = null
      } else {
        startDate = formatDateTime(this.searchVFEcharts.startEndDate[0], this.searchVFEcharts.selectDateState)
        endDate = formatDateTime(this.searchVFEcharts.startEndDate[1], this.searchVFEcharts.selectDateState)

        // console.log(startDate, endDate);
      }


      // 加载Echarts loading效果
      this.myChart.showLoading({
        text: 'loading',
        color: '#c23531',
        textColor: '#000',
        maskColor: 'rgba(255, 255, 255, 0.2)',
        zlevel: 0,
      })

      const { data: res } = await this.$http.get("/system/operational_analysis/timeAnalysis", {
        params: {
          type: selectDateState, // 日期类型
          beginDate: startDate,
          endDate: endDate,
        }
      })
      // console.log(res);
      if (res.state == "200") {

        // 隐藏echart的loadnig效果
        this.myChart.hideLoading();

        this.spotNameListLen = res.data.spotName.length
        this.disabledInput = false

        this.sectionMessage = res.data.message[0]

        var resultData = Object.assign({}, res.data)
        console.log(resultData);

        var seriesList = []
        // 遍历resultData
        for (let key in resultData) {
          // 找到日期字符串
          if (isNaN(key) && !isNaN(Date.parse(key))) {
            console.log(key);

            seriesList.push({
              name: key,
              type: 'bar',
              stack: 'Ad',
              emphasis: {
                focus: 'series'
              },
              data: resultData[key]
            })
          }
        }

        var spotNameList = resultData["spotName"]

        this.option.xAxis[0].data = spotNameList
        this.option.series = seriesList

        this.myChart.setOption(this.option)
      }

      return res
    },

    // 日期选项值发生变化时触发
    onDateChange() {
      // console.log(this.searchValidateForm.selectDateState);

      if (this.searchValidateForm.selectDateState == "1") { // 按月查询
        this.dateType = "monthrange"
      } else if (this.searchValidateForm.selectDateState == "2") { // 按年查询
        this.dateType = "yearrange"
      }
    },

    // 表格排序触发
    onSortChange({ column, prop, order }) {
      console.log(column, prop, order);
      if (order == "ascending") {
        order = "asc"
      } else if (order == "descending") {
        order = "desc"
      }

      if (!order) {
        this.order = null
      } else {
        this.order = `${prop} ${order}`
      }
      this.initTableData()
    },

    /**
     * 画echart图表
     */
    drawEcharts() {
      this.myChart = this.$echarts.init(document.getElementById("myChart"));

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        legend: {},
        dataZoom: [
          {
            type: "inside",
            start: 0,
            end: 10,
          },
          {
            type: "slider",
            // show: true,
            // start: dataAxis.length - 6,
            // end: dataAxis.length - 1,
          },
        ],
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: ['温榆河公园', '温榆河公园', '温榆河公园', '温榆河公园', '温榆河公园', '温榆河公园', '温榆河公园'],
            axisLabel: {
              rotate: 40
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value} 小时'
            }
          }
        ],
        series: [

        ]
      };

      this.myChart.setOption(this.option);

      // 图表自适应
      window.addEventListener("resize", () => {
        this.myChart.resize();
      });
    },

    /**
     * 查询公司名称获取列表数据
     */
    async getTableData() {
      var startEndDateValidateFormList = []
      var startEndDateVFList = []

      startEndDateValidateFormList = [
        formatDateTime(this.searchValidateForm.startEndDate[0], this.searchValidateForm.selectDateState),
        formatDateTime(this.searchValidateForm.startEndDate[1], this.searchValidateForm.selectDateState)
      ]

      startEndDateVFList = [
        formatDateTime(this.searchVF.startEndDate[0], this.searchVF.selectDateState),
        formatDateTime(this.searchVF.startEndDate[1], this.searchVF.selectDateState)
      ]

      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.selectDateState == this.searchVF.selectDateState && startEndDateValidateFormList.toString() == startEndDateVFList.toString()) {
        // 隐藏echart的loadnig效果
        // this.myChart.hideLoading();
        return
      }

      // 保存当前搜索的内容
      this.searchVF.selectDateState = this.searchValidateForm.selectDateState
      this.searchVF.startEndDate = this.searchValidateForm.startEndDate

      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      var res = await this.initTableData()
      // console.log(res);
    },

    /**
     * 查询公司名称获取Echarts数据
     */
    async getEchartsData() {
      var startEndDateValidateFormList = []
      var startEndDateVFEchartsList = []

      startEndDateValidateFormList = [
        formatDateTime(this.searchValidateForm.startEndDate[0], this.searchValidateForm.selectDateState),
        formatDateTime(this.searchValidateForm.startEndDate[1], this.searchValidateForm.selectDateState)
      ]

      startEndDateVFEchartsList = [
        formatDateTime(this.searchVFEcharts.startEndDate[0], this.searchVFEcharts.selectDateState),
        formatDateTime(this.searchVFEcharts.startEndDate[1], this.searchVFEcharts.selectDateState)
      ]

      // 如果输入框中的内容等于上次搜索的内容，则直接返回
      if (this.searchValidateForm.selectDateState == this.searchVFEcharts.selectDateState && startEndDateValidateFormList.toString() == startEndDateVFEchartsList.toString()) {

        return
      }

      // 保存当前搜索的内容
      this.searchVFEcharts.selectDateState = this.searchValidateForm.selectDateState
      this.searchVFEcharts.startEndDate = this.searchValidateForm.startEndDate
      // 否则调用接口，请求数据，为tableData重新赋值
      console.log("调用接口");
      var res = await this.initEchartsData()
      // console.log(res);
    },

    /**
     * 新增签约信息
     */
    async addTableData(data) {
      // console.log(this.validateForm.charge);
      // console.log(this.validateForm.tax);
      // console.log(this.validateForm.proportion);
      const { data: res } = await this.$http.post("/system/operational_analysis/fitAdd", this.$qs.stringify(data))
      // console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
          offset: 100,
          center: true,
          duration: 1000,
        })
        this.initTableData()
        this.initEchartsData()
      } else {
        this.$message({
          message: res.msg,
          type: "error",
          offset: 100,
          center: true,
          duration: 3000,
        })
      }
    },

    /**
     * 编辑签约信息
     */
    async editTableData(data) {
      const { data: res } = await this.$http.post("/system/operational_analysis/fitEdit", this.$qs.stringify(data))
      // console.log(res);
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: "success",
          offset: 100,
          center: true,
          duration: 1000,
        })
        this.initTableData()
        this.initEchartsData()
      } else {
        this.$message({
          message: res.msg,
          type: "error",
          offset: 100,
          center: true,
          duration: 3000,
        })
      }
    },

    /**
     * 景区统计设置删除
     * @params id: 要删除的景区id
     */
    async deleteTableData(id) {
      const { data: res } = await this.$http.delete("/system/operational_analysis/fitDelete", {
        params: {
          id: id
        }
      })
      this.initTableData()
      this.initEchartsData()
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.startEndDate = [
        new Date(),
        new Date()
      ]

      this.searchValidateForm.selectDateState = "1"
      this.dateType = "monthrange"

      // 重新获取表格数据
      this.getTableData()
      this.getEchartsData()
    },

    // 统计分析(显示柱状图)
    onStatGrossProfit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = false
          // console.log(this.searchValidateForm);
          // echarts loading加载
          this.getEchartsData()
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },

    // 导出报表（导出柱状图）
    onExportReport() {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById("exportReport")
        exportReportEle.href = this.myChart.getDataURL()
      })
    },

    // 点击景区统计设置按钮时触发
    onSetting(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.getTableData()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = "add"
    },

    // 点击编辑时触发
    editRow(index, row) {
      console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框

      var rowObj = Object.assign({}, row) // 深拷贝row对象

      this.validateForm = {
        id: rowObj.ID,
        begin: rowObj.BEGIN,
        end: rowObj.END
      }
      this.dialogState = "edit"
    },

    // 点击删除时触发
    deleteRow(index, row) {
      // console.log(index, row);
      this.$confirm('此操作将永久删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteTableData(row.ID)
        this.$message({
          type: 'success',
          message: '删除成功!',
          offset: 100,
          center: true,
          duration: 1000,
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除',
          offset: 100,
          center: true,
          duration: 1000,
        });
      });
    },

    // dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields(); // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.validateForm);
          if (this.dialogState == "add") {
            // 新增公司信息
            console.log("新增公司信息");
            this.addTableData(this.validateForm)
          } else if (this.dialogState == "edit") {
            // 编辑公司信息
            console.log("编辑公司信息");
            this.editTableData(this.validateForm)
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields(); // 重置表单
          this.getValidateForm() // 清空表单
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    activeFixed() {
      return (value) => {
        // // 冻结左侧“合作主体、合作公司、收款单位、景区名称、月份”
        // if (value == "SECTION" || value == "spotName" || value == "payee" || value == "spotName" || value == "month") {
        //   return "left"
        // }
      }
    },

    tableContentValue() {
      return (row, value) => {
        var result = ""
        // 如果当前列为“同比、环比”并且值不为空，则在数字后面加上百分号
        if ((value == "tb" || value == "hb") && !!row[value]) {
          result = row[value] + "%"
        } else {
          result = row[value]
        }
        return result
      }
    },

    sortable() {
      return (value) => {
        var result = ""
        if (value == "spotName" || value == "机器人型号字段" || value == "运营模式字段") {
          result = false
        } else {
          result = "custom"
        }
        return result
      }
    },

    // // 提交表单验证
    // validateFormRules() {
    //   return {
    //     // 景区排序分段统计时长
    //     begin: [
    //       { validator: this.checkRequireNumberMethod('景区排序分段统计时长', this.spotNameListLen), required: true, trigger: ['blur', 'change'] }
    //     ],
    //   }
    // }
  }
}
</script>

